import { MessageBox } from 'element-ui'
//==JSLodopCLodop==
var CreatedOKLodopObject, CLodopIsLocal, CLodopJsState
//==CLodop():==
function needCLodop() {
  try {
    var ua = navigator.userAgent
    if (ua.match(/Windows\sPhone/i)) return true
    if (ua.match(/iPhone|iPod|iPad/i)) return true
    if (ua.match(/Android/i)) return true
    if (ua.match(/Edge\D?\d+/i)) return true

    var verTrident = ua.match(/Trident\D?\d+/i)
    var verIE = ua.match(/MSIE\D?\d+/i)
    var verOPR = ua.match(/OPR\D?\d+/i)
    var verFF = ua.match(/Firefox\D?\d+/i)
    var x64 = ua.match(/x64/i)
    if (!verTrident && !verIE && x64) return true
    else if (verFF) {
      verFF = verFF[0].match(/\d+/)
      if (verFF[0] >= 41 || x64) return true
    } else if (verOPR) {
      verOPR = verOPR[0].match(/\d+/)
      if (verOPR[0] >= 32) return true
    } else if (!verTrident && !verIE) {
      var verChrome = ua.match(/Chrome\D?\d+/i)
      if (verChrome) {
        verChrome = verChrome[0].match(/\d+/)
        if (verChrome[0] >= 41) return true
      }
    }
    return false
  } catch (err) {
    return true
  }
}
//==CLodopJS,800018000():==
if (needCLodop()) {
  var head =
    document.head ||
    document.getElementsByTagName('head')[0] ||
    document.documentElement

  var JS1 = document.createElement('script')
  JS1.src = 'http://localhost:8000/CLodopfuncs.js?priority=1'
  head.insertBefore(JS1, head.firstChild)

  var JS2 = document.createElement('script')
  JS2.src = 'http://localhost:18000/CLodopfuncs.js?priority=0'
  head.insertBefore(JS2, head.firstChild)

  CLodopIsLocal = !!(JS1.src + JS2.src).match(/\/\/localho|\/\/127.0.0./i)

  if (JS1.attachEvent) {
    CLodopJsState = 'loading'
    var onChange = function () {
      if (window.event.srcElement.readyState == 'loaded')
        CLodopJsState = 'complete'
    }
    JS1.attachEvent('onreadystatechange', onChange)
    JS2.attachEvent('onreadystatechange', onChange)
  }
}
// /
//==LODOP,:==
function getLodop(oOBJECT, oEMBED) {
  let exeUrl = 'http://www.lodop.net/download.html'
  var strHtmInstall =
    "<br><font color='#FF00FF'>!<a href=" +
    exeUrl +
    " target='_blank'></a>,</font>"
  var strHtmUpdate =
    "<br><font color='#FF00FF'>!<a href=" +
    exeUrl +
    " target='_blank'></a>,</font>"
  var strHtm64_Install =
    "<br><font color='#FF00FF'>!<a href=" +
    exeUrl +
    " target='_blank'></a>,</font>"
  var strHtm64_Update =
    "<br><font color='#FF00FF'>!<a href=" +
    exeUrl +
    " target='_blank'></a>,</font>"
  var strHtmFireFox =
    "<br><font color='#FF00FF'>LodopnpActiveXPLugin,->-></font>"
  var strHtmChrome =
    "<br><font color='#FF00FF'>(</font>"
  var strCLodopInstall_1 =
    "<br><font color='#FF00FF'>WebCLodop<a href=" +
    exeUrl +
    " target='_blank'></a>,"
  var strCLodopInstall_2 =
    "<br><a href='CLodop.protocol:setup' target='_self'></a>"
  var strCLodopInstall_3 = '</font>'
  var strCLodopUpdate =
    "<br><font color='#FF00FF'>WebCLodop!<a href=" +
    exeUrl +
    " target='_blank'></a>,,</font>"
  var LODOP
  try {
    var ua = navigator.userAgent
    var isIE = !!ua.match(/MSIE/i) || !!ua.match(/Trident/i)
    if (needCLodop()) {
      try {
        LODOP = window.getCLodop()
      } catch (err) {
        ;('')
      }
      if (
        !LODOP &&
        (document.readyState !== 'complete' ||
          (isIE && CLodopJsState == 'loading'))
      ) {
        alert('.')
        return
      }
      if (!LODOP) {
        MessageBox.alert(
          strCLodopInstall_1 +
            (CLodopIsLocal ? strCLodopInstall_2 : '') +
            strCLodopInstall_3,
          '',
          {
            dangerouslyUseHTMLString: true,
          },
        )
        //document.body.innerHTML = strCLodopInstall_1 + (CLodopIsLocal ? strCLodopInstall_2 : '') + strCLodopInstall_3 + document.body.innerHTML
        return
      } else {
        if (window.CLODOP.CVERSION < '3.0.8.3') {
          MessageBox.alert(strCLodopUpdate, '', {
            dangerouslyUseHTMLString: true,
          })
          //document.body.innerHTML = strCLodopUpdate + document.body.innerHTML
        }
        if (oEMBED && oEMBED.parentNode) oEMBED.parentNode.removeChild(oEMBED)
        if (oOBJECT && oOBJECT.parentNode)
          oOBJECT.parentNode.removeChild(oOBJECT)
      }
    } else {
      var is64IE = isIE && !!ua.match(/x64/i)
      //==Lodop,:==
      if (oOBJECT || oEMBED) {
        if (isIE) LODOP = oOBJECT
        else LODOP = oEMBED
      } else if (!CreatedOKLodopObject) {
        LODOP = document.createElement('object')
        LODOP.setAttribute('width', 0)
        LODOP.setAttribute('height', 0)
        LODOP.setAttribute(
          'style',
          'position:absolute;left:0px;top:-100px;width:0px;height:0px;',
        )
        if (isIE)
          LODOP.setAttribute(
            'classid',
            'clsid:2105C259-1E0C-4534-8141-A753534CB4CA',
          )
        else LODOP.setAttribute('type', 'application/x-print-lodop')
        document.documentElement.appendChild(LODOP)
        CreatedOKLodopObject = LODOP
      } else LODOP = CreatedOKLodopObject
      //==Lodop:==
      if (!LODOP || !LODOP.VERSION) {
        if (ua.indexOf('Chrome') >= 0)
          MessageBox.alert(strHtmChrome, '', {
            dangerouslyUseHTMLString: true,
          })
        //document.body.innerHTML = strHtmChrome + document.body.innerHTML
        if (ua.indexOf('Firefox') >= 0)
          MessageBox.alert(
            (is64IE ? strHtm64_Install : strHtmInstall) + strHtmFireFox,
            '',
            { dangerouslyUseHTMLString: true },
          )
        // 	document.body.innerHTML = strHtmFireFox + document.body.innerHTML
        // document.body.innerHTML = (is64IE ? strHtm64_Install : strHtmInstall) + document.body.innerHTML
        return LODOP
      }
    }
    if (LODOP.VERSION < '6.2.2.6') {
      if (!needCLodop())
        MessageBox.alert(is64IE ? strHtm64_Update : strHtmUpdate, '', {
          dangerouslyUseHTMLString: true,
        })
      //document.body.innerHTML = (is64IE ? strHtm64_Update : strHtmUpdate) + document.body.innerHTML
    }
    //===():==

    //=======================================================
    return LODOP
  } catch (err) {
    alert('getLodop:' + err)
  }
}

export default getLodop
