import getLodop from './LodopFuncs'
import cloneDeep from 'lodash/cloneDeep'
import {
  tableTempTohtml,
  imageTempTohtml,
  strTempToValue,
  htmlTempTohtml,
} from './tools'

let strCompanyName = ''
let strLicense = ''
let strLicenseA = ''
let strLicenseB = ''

export default {
  print,
  preview,
  previewTemp,
  setLicenses,
  getPrintList,
  setPrinter,
}

/**
 * 
 */
function getPrintList() {
  let LODOP = getLodop()
  if (!LODOP) {
    return
  }

  let count = LODOP.GET_PRINTER_COUNT()
  let prints = []
  Array(count)
    .fill(null)
    .map((...args) => {
      prints.push({
        index: args[1],
        label: LODOP.GET_PRINTER_NAME(args[1]),
      })
      return {
        index: args[1],
        label: LODOP.GET_PRINTER_NAME(args[1]),
      }
    })
  return prints
}

/**
 * 
 * @param {*} LODOP lodop
 * @param {*} index 
 */
function setPrinter(LODOP, index) {
  if (!LODOP) {
    LODOP = getLodop()
  }
  LODOP.SET_PRINTER_INDEX(index)
}

/**
 * Lodop
 * @param {*} licenseInfo
 */
function setLicenses(licenseInfo) {
  strCompanyName = licenseInfo.strCompanyName || ''
  strLicense = licenseInfo.strLicense || ''
  strLicenseA = licenseInfo.strLicenseA || ''
  strLicenseB = licenseInfo.strLicenseB || ''
}
/**
 * 
 * @param {*Object} temp 
 * @param {*Array} data 
 * @param {*String} mode 
 * @param {*Number} index 
 */
function print(temp, data, mode, index) {
  let LODOP = _CreateLodop(
    temp.title,
    temp.width,
    temp.height,
    temp.pageWidth,
    temp.pageHeight,
    mode,
    index,
  )

  let tempItems = cloneDeep(temp.tempItems)
  let printContent = _TempParser(tempItems, data)
  if (printContent.length > 1) {
    // 
    printContent.forEach((aPrint, index) => {
      LODOP.NewPageA()
      aPrint.forEach((printItem) => {
        _AddPrintItem(LODOP, printItem, index)
      })
    })
  } else {
    // 
    printContent[0].forEach((printItem) => {
      _AddPrintItem(LODOP, printItem)
    })
  }

  let flag = LODOP.PRINT()
  return flag
}

/**
 * 
 * @param {*Object} temp 
 * @param {*Array} data 
 * @param {*String} mode 
 * @param {*Number} index 
 */
function preview(temp, data, mode, index) {
  let LODOP = _CreateLodop(
    temp.title,
    temp.width,
    temp.height,
    temp.pageWidth,
    temp.pageHeight,
    mode,
    index,
  )
  let tempItems = cloneDeep(temp.tempItems)
  let printContent = _TempParser(tempItems, data)
  if (data.length > 1) {
    // 
    printContent.forEach((aPrint, index) => {
      LODOP.NewPageA()
      aPrint.forEach((printItem) => {
        _AddPrintItem(LODOP, printItem, index)
      })
    })
  } else {
    // 
    printContent[0].forEach((printItem) => {
      _AddPrintItem(LODOP, printItem)
    })
  }

  let flag = LODOP.PREVIEW()
  return flag
}

/**
 * 
 * @param {*Object} temp 
 * @param {*String} mode 
 * @param {*Number} index 
 */
function previewTemp(temp, mode, index) {
  let LODOP = _CreateLodop(
    temp.title,
    temp.width,
    temp.height,
    temp.pageWidth,
    temp.pageHeight,
    mode,
    index,
  )

  let printContent = _TempParser(temp.tempItems)
  printContent[0].forEach((printItem) => {
    _AddPrintItem(LODOP, printItem)
  })
  let flag = LODOP.PREVIEW()
  return flag
}

/**
 * LODOP 
 * @param pageName 
 * @param width (px)
 * @param height (px)
 * @param pageWidth (mm)
 * @param pageHeight (mm)
 * @param top (px)
 * @param left (px)
 */
function _CreateLodop(
  pageName,
  width,
  height,
  pageWidth = 0,
  pageHeight = 0,
  top = 0,
  left = 0,
  mode = 'Auto-Width',
  index = 0,
) {
  let LODOP = getLodop()

  LODOP.SET_PRINT_MODE('PRINT_PAGE_PERCENT', mode)
  LODOP.SET_PRINTER_INDEX(index)
  // console.log(strCompanyName, strLicense, strLicenseA, strLicenseB)

  // 
  LODOP.SET_LICENSES(strCompanyName, strLicense, strLicenseA, strLicenseB)

  LODOP.PRINT_INITA(top, left, width, height, pageName)
  LODOP.SET_PRINT_PAGESIZE(
    1,
    pageWidth ? pageWidth + 'mm' : 0,
    pageHeight ? pageHeight + 'mm' : 0,
    '',
  )

  return LODOP
}

/**
 * 
 * @param {*Array} tempItem 
 * @param {Array} data ,
 * @return {Array} datanull
 */
function _TempParser(tempItem, data) {
  let temp = cloneDeep(tempItem)
  //
  //item.style.AutoHeight == true
  let flag = temp.findIndex((item) => item.style.AutoHeight)
  if (flag != -1) {
    let autoItem = temp[flag]
    temp.splice(flag, 1)
    temp.unshift(autoItem)
    // 
    temp.forEach((item) => {
      // topleft,style.LinkedItem
      if (item.top > autoItem.top && item.style.ItemType == 0) {
        item.top = item.top - autoItem.top - autoItem.height
        item.left = item.left - autoItem.left
        item.style.LinkedItem = 1
      }
    })
  }

  if (data && data.length > 0) {
    // 
    let tempContent = []
    data.forEach((dataItem) => {
      let conItem = temp.map((tempItem) => {
        let item = cloneDeep(tempItem)
        if (item.name) {
          item.defaultValue = dataItem[item.name]
          item.value = strTempToValue(item.value, item.defaultValue)
        }
        return item
      })
      tempContent.push(conItem)
    })
    return tempContent
  } else {
    return [temp]
  }
}

/**
 * 
 * @param {lodop} LODOP 
 * @param {Object} printItem 
 * @param {Number} pageIndex 
 */
function _AddPrintItem(LODOP, tempItem, pageIndex = 0) {
  let printItem = cloneDeep(tempItem)
  // TempItemStyleLodopItemStyle
  let lodopStyle = _createLodopStyle(printItem.style)

  // 
  if (lodopStyle.LinkedItem == 1) {
    lodopStyle.LinkedItem = 1 + pageIndex
  }
  // 
  switch (printItem.type) {
    case 'braid-txt':
      LODOP.ADD_PRINT_TEXT(
        printItem.top,
        printItem.left,
        printItem.width,
        printItem.height,
        printItem.value,
      )
      break
    case 'bar-code':
      LODOP.ADD_PRINT_BARCODE(
        printItem.top,
        printItem.left,
        printItem.width,
        printItem.height,
        lodopStyle.codeType,
        printItem.value,
      )
      break
    case 'braid-html':
      {
        let html = htmlTempTohtml(printItem.defaultValue, printItem.style)
        if (lodopStyle.AutoHeight) {
          LODOP.ADD_PRINT_HTM(
            printItem.top,
            printItem.left,
            printItem.width,
            'BottomMargin:' + lodopStyle.BottomMargin + 'mm',
            html,
          )
        } else {
          LODOP.ADD_PRINT_HTM(
            printItem.top,
            printItem.left,
            printItem.width,
            printItem.height,
            html,
          )
        }
      }
      break
    case 'braid-table':
      {
        let html = tableTempTohtml(
          printItem.columns ? printItem.columns : [],
          printItem.defaultValue,
          printItem.style,
        )
        if (lodopStyle.AutoHeight) {
          LODOP.ADD_PRINT_TABLE(
            printItem.top,
            printItem.left,
            printItem.width,
            'BottomMargin:' + lodopStyle.BottomMargin + 'mm',
            html,
          )
        } else {
          LODOP.ADD_PRINT_TABLE(
            printItem.top,
            printItem.left,
            printItem.width,
            printItem.height,
            html,
          )
        }
      }
      break
    case 'braid-image':
      {
        let html = imageTempTohtml(printItem.value)
        LODOP.ADD_PRINT_IMAGE(
          printItem.top,
          printItem.left,
          printItem.width,
          printItem.height,
          html,
        )
      }
      break
    default:
      ''
  }
  // 
  Object.keys(lodopStyle).forEach((key) => {
    LODOP.SET_PRINT_STYLEA(0, key, lodopStyle[key])
  })
  // LodopStyle
  let defaultLodopStyle = printItem.lodopStyle
  if (defaultLodopStyle) {
    Object.keys(defaultLodopStyle).forEach((key) => {
      LODOP.SET_PRINT_STYLEA(0, key, defaultLodopStyle[key])
    })
  }
}

/**
 * lodop
 * @param style 
 * @returns lodop
 */
function _createLodopStyle(style) {
  let lodopStyle = {
    zIndex: style.zIndex,
  }

  for (let key in style) {
    if (['Bold', 'Italic', 'Underline', 'ShowBarText'].indexOf(key) > -1) {
      lodopStyle[key] = style[key] ? 1 : 0
    } else if (key === 'Alignment') {
      lodopStyle[key] =
        style[key] == 'left' ? 1 : style[key] == 'center' ? 2 : 3
    } else {
      lodopStyle[key] = style[key]
    }
  }

  return lodopStyle
}
